.footer{
  padding: 2.5em 0;
  margin-top: 1.875em;
  background-color: $lightBlue;
}

.footer-address{
  display: flex;
  align-items: center;
  &__title{
    font-size: 1.25em;
    font-weight: 600;
    color: $primary;
    margin-right: 1em;
  }
  &__street{
    font-size: .8125em;
    letter-spacing: .01em;
  }
}

.footer-contacts{
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  margin-top: 1.875em;
  &__item{
    @include lastM(0 0 1.25em);
    display: flex;
    align-items: center;
    img{
      width: 1em;
      margin-right: .625em;
    }
    &--phone{
      color: $primary;
    }
  }
}

.footer-socials{
  display: flex;
  align-items: center;
  &__item{
    margin-right: .625em;
    &:hover{
      path{
        fill: $primary;
      }
    }
  }
}
