@import 'vars';
@import 'components/_text.scss';
@import 'components/_buttons.scss';
//@import "components/_inputs.scss";
//@import "components/_pagination.scss";
//@import "components/_tabs.scss";
.socials{
  @include grid100gap(.5em);
  &__item{
    display: flex;
    align-items: center;
    justify-content: space-between;
    height: 3.3125em;
    padding: 0 1.25em;
    border-radius: .375em;
    box-shadow: 0 2px 3px 0 rgba(73, 73, 73, 0.25);
    background: $primary2, linear-gradient(0deg, #20b038 0%, #60d66a 100%);
    font-weight: 600;
    color: #fff;
    &--vk{
      background: #0078ff;
    }
    &--ig{
      background-image: url(../img/ig-bg.png);
      background-size: cover;
      background-position: center;
    }
    &:hover{
      color: #fff;
      opacity: .9;
    }
    img{
      width: 1.3125em;
      margin-left: 1em;
    }
  }
}

.spoiler{
  &:not(._active){
    .spoiler__hidden{
      height: 0;
    }
  }
  &__toggle{
    cursor: pointer;
  }
  &__hidden{
    overflow: hidden;
    transition: all .5s;
  }
}
