@import "../vars";
.m-btn {
    display: flex;
    align-items: center;
    justify-content: center;
    transition: all ease-out .225s;
    height: 3.3125em;
    padding: 0 1.25em;
    border-radius: .375em;
    box-shadow: 0 2px 3px 0 rgba(73, 73, 73, 0.25);
    font-weight: 600;
    &:focus {
        box-shadow: none;
    }
    span{
        font-size: 1em;
        line-height: 120%;
    }
}

.m-btn-primary{
    color: #fff;
    background-color: $primary2;
    &:hover{
        color: #fff;
        background-color: darken($primary2,10%);
    }
}

.m-btn-gradient{
    color: #fff;
    background-color: $primary2;
    background-image: url(../img/ig-bg.png);
    background-size: cover;
    background-position: center;
    &:hover{
        color: #fff;
        opacity: .9;
    }
}
