.main{
  padding: 7.875em 0 1.875em;
  &__desc{
    margin-top: 1.25em;
  }
  &__items{
    display: grid;
    grid-template-columns: repeat(2,calc(50% - .625em));
    gap: 1.25em;
    margin-top: .875em;
  }
  .socials{
    margin-top: 1.625em;
    position: relative;
    z-index: 2;
  }
  &__img{
    display: block;
    width: 100%;
    margin-top: -2.875em;
  }
}

.main-item{
  display: flex;
  align-items: center;
  flex-direction: column;
  text-align: center;
  &__icon{
    height: 1.875em;
    flex-shrink: 0;
    margin-bottom: .625em;
  }
  a{
    color: $primary2;
    text-decoration: underline;
    &:hover{
      color: $primary;
    }
  }
}

.reviews-site{
  position: relative;
  z-index: 2;
  padding: 1em .625em;
  box-shadow: -13px 20px 20px -11px rgba(0, 0, 0, 0.15);
  background-color: #fff;
  margin-top: -.875em;
  border-radius: .375em;
  &__top{
    display: flex;
    align-items: center;
    justify-content: space-between;
  }
  &__link{
    color: $primary;
    &:hover{
      color: $primary2;
    }
  }
  &__items{
    display: grid;
    grid-template-columns: repeat(8,calc(12.5% - 1.18125em / 8));
    gap: .16875em;
    margin-top: .75em;
  }
}

.avatar{
  @include imgCover(100%);
  border-radius: 50%;
}

.m-section{
  padding: 1.875em 0;
}

.choose{
  &__inner{
    padding: 1.875em 0;
    background-color: $lightBlue;
  }
  &__img{
    width: 100%;
    display: block;
    margin-top: -.625em;
  }
  &__top{
    padding: 0 1.875em;
    @include grid100gap(1.25em);
  }
  &__nav{
    padding: 0 1.875em;
    margin-top: .625em;
  }
}

.instagram{
  &__inner{
    padding: 1.875em 1em;
    background-color: $lightBlue;
  }
  &__img{
    padding: 1.25em .625em 0;
    img{
      display: block;
      width: 100%;
    }
  }
  &__nav{
    margin-top: -2.25em;
    position: relative;
    z-index: 2;
  }
}

.production{
  &__items{
    margin-top: .625em;
    margin-left: -1.25em;
    @include grid100gap(.625em);
    width: calc(100% + 2.5em);
  }
}

.text{
  color: $black2;
  p{
    font-size: .875em;
    line-height: 140%;
    @include lastM(0 0 1em);
  }
}

.production-card{
  padding: 1.25em;
  &:nth-child(even){
    background-color: $lightBlue;
  }
  &__img{
    margin-top: 1.25em;
    border-radius: .375em;
    @include imgCover(62.6%);
  }
  &__text{
    margin-top: 1.25em;
  }
}
