.stars{
  overflow: hidden;
}

.swiper-pagination{
  position: static;
  margin-top: 1.875em;
  width: 100%;
  @include center;
  flex-wrap: wrap;
  padding-bottom: .625em;
  &-bullet{
    margin: .625em .3125em 0 !important;
    width: 2.25em;
    height: 3px;
    border-radius: .25em;
    background-color: $gray;
    opacity: 1;
    position: relative;
    &:before{
      content: '';
      position: absolute;
      left: 0;
      top: 50%;
      width: 100%;
      height: 200%;
      transform: translateY(-50%);
    }
    &-active{
      background-color: $primary;
    }
  }
}

.stars-slider{
  margin-top: 1.875em;
  .swiper{
    &-slide{
      width: 13.75em;
      padding-right: 1.875em;
    }
  }
}

.star-card{
  display: block;
  &__img{
    @include imgCover(178%);
    border-radius: .625em;
  }
  &__content{
    margin-top: 1.25em;
    @include grid100gap(.625em);
  }
}

.reviews{
  margin: 1.875em 0;
  padding: 3.75em 0;
  background-color: $lightBlue;
  overflow: hidden;
}

.reviews-slider{
  margin-top: 1.875em;
  .swiper{
    &-slide{
      width: 16.625em;
      padding-right: 1.25em;
    }
  }
}

.review-card{
  display: block;
  &__img{
    @include imgCover(133%);
    border-radius: .625em;
    border: 1px solid $gray;
  }
  &__rating{
    display: flex;
    margin-top: 1em;
    img{
      width: 1.25em;
    }
  }
  &__wrap{
    margin-top: .875em;
    display: flex;
    justify-content: space-between;
    font-weight: 500;
  }
  &__city{
    display: flex;
    align-items: center;
    margin-top: .875em;
    font-weight: 500;
    img{
      width: 1.25em;
      margin-right: .625em;
    }
  }
}

.faq{
  &__items{
    margin-top: 1.5625em;
  }
}

.qa-card{
  padding: .5em 1em;
  border-top: 1px solid $lightBlue;
  &:last-child{
    border-bottom: 1px solid $lightBlue;
  }
  &._active{
    .qa-card__title{
      color: $primary;
    }
    .qa-card__btn{
      background-color: $primary;
      ._view1{
        opacity: 0;
      }
      ._view2{
        opacity: 1;
      }
    }
  }
  &__top{
    display: flex;
    align-items: center;
  }
  &__num{
    font-weight: 700;
    color: $primary;
    min-width: 1.875em;
    margin-right: .3125em;
    flex-shrink: 0;
    span{
      font-size: 1em;
      line-height: 110%;
    }
  }
  &__title{
    font-size: 1em;
    line-height: 140%;
    font-weight: 600;
    margin-right: 2em;
    transition: all .3s;
  }
  &__btn{
    align-self: center;
    margin-left: auto;
    flex-shrink: 0;
    width: 2.5em;
    height: 2.5em;
    position: relative;
    border-radius: 50%;
    background-color: $lightBlue;
    svg{
      @include centerAbsolute;
      width: 1em;
    }
    ._view2{
      opacity: 0;
    }
  }
  .text{
    padding-top: 1em;
  }
}
